<template>
  <b-overlay :show="show" rounded="sm">
    <div ref="content">
      <div class="title-fw normal-fs mb-3 mobileJustify">
        {{ $t("DOWNLOADOBJ") }}
      </div>
      <b-form @submit.prevent="submit">
        <b-row>
          <b-col cols="12">
            <b-button variant="primary" class="px-16 required-btnAny">{{
              $t("REQUIREDBUTTON")
            }}</b-button>
            <label class="highLightHeaderLabel pl-2">{{ $t("OFFICE") }}</label>
            &nbsp;&nbsp;
          </b-col>
          <!-- <b-col id="options" cols="12">
            <b-form-select
              :plain="true"
              :options="getOfficeOptions"
              v-model="office"
              class="office-select"
            ></b-form-select>
          </b-col> -->
          <b-col id="options" cols="12" xl="6">
            <b-form-select
              :options="getOfficeOptions"
              v-model="office"
              class="optionClass dropDownButton"
            ></b-form-select>
          </b-col>
        </b-row>
        <br />

        <b-row>
          <b-col cols="12" xl="6">
            <b-row>
              <b-col cols="12">
                <b-button variant="primary" class="px-16 required-btnAny">{{
                  $t("REQUIREDBUTTON")
                }}</b-button>
                <label class="highLightHeaderLabel pl-2">{{
                  $t("GETSTARTDAY")
                }}</label>
              </b-col>
              <b-col cols="12" class="mb-3">
                <b-input-group>
                  <b-input-group-append>
                    <DatePicker
                      v-model.trim="dateStart"
                      format="YYYY/MM/DD"
                    ></DatePicker>
                  </b-input-group-append>
                </b-input-group>
              </b-col>
            </b-row>
          </b-col>
          <b-col cols="12" xl="6">
            <b-row>
              <b-col cols="12">
                <b-button variant="primary" class="px-16 required-btnAny">{{
                  $t("REQUIREDBUTTON")
                }}</b-button>
                <label class="highLightHeaderLabel pl-2">{{
                  $t("GETENDDAY")
                }}</label>
              </b-col>
              <b-col id="options" cols="12">
                <b-input-group>
                  <b-input-group-append>
                    <DatePicker
                      v-model.trim="dateEnd"
                      format="YYYY/MM/DD"
                    ></DatePicker>
                  </b-input-group-append>
                </b-input-group>
              </b-col>
              <div v-if="dateFlag" class="dateError ml-3">
                {{ $t("DATEERROR") }}
              </div>
            </b-row>
          </b-col>
        </b-row>
        <b-row>
          <b-col
            cols="12"
            xl="12"
            class="text-center font-block font-weight-bold mt-4"
            >{{ $t("MAX12MON") }}</b-col
          >
        </b-row>
        <b-row>
          <b-col
            cols="12"
            xl="12"
            class="d-flex align-items-center justify-content-center mt-3"
          >
            <b-button
              id="downloadBtn"
              @click="download"
              :disabled="
                !dateStart || !dateEnd || !office || dateFlag || status
              "
              variant="primary"
              class="journal-download-btn"
              ><span>{{ $t("DOWNLOAD") }}</span
              ><i v-if="status" class="fa fa-spinner fa-spin"
            /></b-button>
          </b-col>
        </b-row>

        <b-row class="text-center mt-3">
          <b-col cols="12">
            <span v-if="checkPaidPlan">{{ dataLoad }}</span>
            <span v-else>{{ dataAckString }}</span>
          </b-col>
        </b-row>
      </b-form>
    </div>
    <b-row class="mt-5">
      <b-col
        cols="12"
        class="d-flex align-items-center justify-content-end mbljournalreturnBtn"
      >
        <b-button
          @click="returnToJournalImport"
          variant="dark"
          class="d-flex date-fs align-items-center justify-content-center returnBtn"
        >
          <i class="fa fa-caret-left fa-2x mr-2" aria-hidden="true"></i
          >{{ $t("RETURNJOURNALIMPORT") }}</b-button
        >
      </b-col>
    </b-row>
  </b-overlay>
</template>

<script>
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
import _ from "lodash";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { Settings } from "luxon";
import { mapGetters } from "vuex";
Settings.defaultLocale = "ja";
import { endPoint } from "@/shared/endPoint";
import i18n from "@/plugins/i18n";

export default {
  name: "FileDownload",
  components: {},
  data() {
    return {
      getOfficeOptions: [
        {
          text: this.$t("OFFICENAMESELECTION"),
          value: "",
        },
      ],
      dateStart: "",
      dateEnd: "",
      formatted: "",
      selected: "",
      office: "",
      journalId: null,
      dataLoading: "",
      checkPaidPlan: false,
      dataAckString: "",
      show: true,
      dateFlag: false,
      dataLoad: i18n.t("DATALOADING"),
      status: false,
    };
  },

  computed: {
    ...mapGetters({
      linkageInfoNo: "getLinkageInfoNo",
    }),
  },
  watch: {
    //validate start date is lesser than end date
    dateStart: function () {
      if (this.dateStart && this.dateEnd) {
        var endDate = new Date(this.formatDate(this.dateEnd));
        var startDate = new Date(this.formatDate(this.dateStart));
        if (
          this.formatDate(this.dateStart) != "" &&
          this.formatDate(this.dateStart) != "" &&
          startDate > endDate
        ) {
          this.dateFlag = true;
        } else {
          this.dateFlag = false;
        }
      } else {
        this.dateFlag = false;
      }
    },
    //validate start date is lesser than end date
    dateEnd: function () {
      if (this.dateStart && this.dateEnd) {
        var endDate = new Date(this.formatDate(this.dateEnd));
        var startDate = new Date(this.formatDate(this.dateStart));
        if (
          this.formatDate(this.dateStart) != "" &&
          this.formatDate(this.dateStart) != "" &&
          startDate > endDate
        ) {
          this.dateFlag = true;
        } else {
          this.dateFlag = false;
        }
      } else {
        this.dateFlag = false;
      }
    },
  },
  mounted() {
    //on page load get officeList
    this.getOfficeList();
  },
  methods: {
    returnToJournalImport() {
      this.$router.push({ name: "journal_import" });
    },
    /**
     * @description function to get the office list
     * @name getOfficeList
     * @param none
     * @returns nothing
     */
    async getOfficeList() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.INTEGRATIONREADCOMPANIES,
        {
          client_no: parseInt(localStorage.getItem("client_account_no")),
          linkage_info_no: this.linkageInfoNo.linkageInfoNo,
        }
      );
      if (data.success) {
        for (let i = 0; i < data.data.companies.length; i++) {
          this.getOfficeOptions.push({
            value: data.data.companies[i].id,
            text: data.data.companies[i].display_name,
          });
        }
        this.show = false;
      } else {
        this.show = false;
      }
    },
    /**
     * @description function to update the journal file status and navigate to file download complete screen
     * @name goToFileDownloadComplete
     * @param none
     * @returns nothing
     */
    async goToFileDownloadComplete() {
      this.dataLoad = i18n.t("DOWNLOADING");
      const { data } = await BasicInfoApis.postAPI(
        endPoint.INTEGRATIONREADJOURNALSTATUS,
        {
          client_no: parseInt(localStorage.getItem("client_account_no")),
          journal_id: this.journalId,
          company_id: this.office,
          linkage_info_no: this.linkageInfoNo.linkageInfoNo,
          start_date: this.formatDate(this.dateStart),
          end_date: this.formatDate(this.dateEnd),
          create_id: parseInt(localStorage.getItem("client_account_no")),
          person_id: parseInt(localStorage.getItem("client_account_no")),
          update_id: parseInt(localStorage.getItem("client_account_no")),
        }
      );
      if (data.success) {
        if (data.message.info === "REQUESTED_DATA_FETCHED") {
          this.status = false;
          // update the store
          this.$store.dispatch("updateDownloadData", {
            downloadData: {
              office: _.find(this.getOfficeOptions, { value: this.office }),
              acquisitionEndDate: this.formatDate(this.dateEnd),
              acquisitionStartDate: this.formatDate(this.dateStart),
            },
          });
          this.$router.push({ name: "file_download_complete" });
        } else {
          this.status = false;
          this.checkPaidPlan = false;
        }
      } else {
        this.status = false;
        if (data.message.code == 4001)
          this.$swal({ type: "info", text: this.$t("NORECORDSFOUND") });
        if (data.message.code == 4002)
          this.$swal({
            type: "info",
            text: this.$t("INVALIDFILECONTENTISSUE"),
          });
        this.checkPaidPlan = false;
      }
    },
    /**
     * @description function to download the jounal file and navigate to download complete screen
     * @name download
     * @param none
     * @return nothing
     */
    async download() {
      this.dataAckString = "";
      this.checkPaidPlan = true;
      this.status = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.INTEGRATIONREADJOURNAL,
        {
          client_no: parseInt(localStorage.getItem("client_account_no")),
          download_type: "csv",
          company_id: this.office,
          start_date: this.formatDate(this.dateStart),
          end_date: this.formatDate(this.dateEnd),
          linkage_info_no: this.linkageInfoNo.linkageInfoNo,
          visible_tags: [
            "partner",
            "item",
            "tag",
            "section",
            "description",
            "wallet_txn_description",
          ],
        }
      );

      if (data.success) {
        if (data.data.journals != undefined) {
          this.journalId = data.data.journals.id;
          this.goToFileDownloadComplete();
        } else {
          this.status = false;
          this.checkPaidPlan = false;
          this.dataAckString = data.data.errors[0].messages[0];
        }
      } else {
        this.status = false;
        this.checkPaidPlan = false;
      }
    },
    /**
     * @description function to format the date
     * @name formatDate
     * @param data
     * @return string 'formated date'
     */
    formatDate(data) {
      var mnths = {
        Jan: "01",
        Feb: "02",
        Mar: "03",
        Apr: "04",
        May: "05",
        Jun: "06",
        Jul: "07",
        Aug: "08",
        Sep: "09",
        Oct: "10",
        Nov: "11",
        Dec: "12",
      };
      var date = data.toString().split(" ");
      return [date[3], mnths[date[1]], date[2]].join("/");
    },
  },
};
</script>

<style lang="scss" scoped>
.highLightHeaderLabel {
  font-weight: 700 !important;
  font-size: 0.8rem !important;
  padding-top: 9px;
  font-size: initial;
}
.fromDatePicker-ml {
  margin-left: -12px !important;
}

.returnBtn {
  background: #bfbfbf 0% 0% no-repeat padding-box !important;
  box-shadow: 0px 0px 30px #0000001a !important;
  border-radius: 4px !important;
  opacity: 1 !important;
}
.mx-datepicker {
  width: 100% !important;
}
@media only screen and (max-width: 768px) {
  .mbljournalreturnBtn {
    justify-content: center !important;
    text-align: center !important;
  }
  .mobileJustify {
    display: flex !important;
    justify-content: center !important;
  }
}

@media only screen and (max-width: 991px) {
  // .mx-datepicker {
  //   width: 300px !important;
  // }
  .toDatePicker-ml {
    margin-left: -12px !important;
  }
}
.optionClass {
  text-align-last: left !important;
  // background: #fff url("../../../assets/pulldown-icon.svg") no-repeat padding-box;
  box-shadow: 0px 0px 30px #00000014;
  border-radius: 50px !important;
  opacity: 1 !important;
  font-weight: 550 !important;
  color: #4b4b4b !important;
  border: none !important;
}
.journal-download-btn {
  min-width: 145px !important;
}
.font-block {
  text-align: left;
  letter-spacing: 0px;
  color: #4b4b4b;
  opacity: 1;
  font-size: 11px;
  font-weight: 700;
}
.required-btnAny {
  padding: 2px 5px 2px 5px !important;
  min-width: 10px !important;
  font-size: 11px;
  background: #033170 0% 0% no-repeat padding-box;
  border-radius: 4px;
  opacity: 1;
  letter-spacing: 0px;
  color: #ffffff;
  opacity: 1;
}

.form-control {
  border-radius: inherit;
  height: 40px;
}

.office-select {
  height: 100%;
  border-radius: 0px;
  border: 1px solid #a9a6a7;
}

.dateError {
  color: red;
}
.input-group-append {
  width: 100% !important;
}
</style>
